"use client";

import { usePathname, useSearchParams } from "next/navigation";
import { cn } from "@/lib/utils";
import { Sidebar } from "@/components/sidebar";
import { MessageSquare, Users, Radio, Phone, Settings } from "lucide-react";
import Link from "next/link";

export function ChatShell({ children, user }: { children: React.ReactNode; user: any }) {
    const pathname = usePathname();
    const searchParams = useSearchParams();
    const isRoomSelected = searchParams.has("roomId");
    const isChatRoot = pathname === "/chat";
    const showSidebarOnMobile = isChatRoot && !isRoomSelected;
    const showMainOnMobile = !showSidebarOnMobile;

    return (
        <div className="flex h-screen bg-background overflow-hidden relative">
            {/* Sidebar Rail */}
            <div className={cn(
                "h-full transition-all duration-300",
                showSidebarOnMobile ? "w-full" : "hidden md:flex md:w-80"
            )}>
                <Sidebar user={user} />
            </div>

            {/* Main Content Area */}
            <main className={cn(
                "flex-1 relative flex flex-col h-full overflow-hidden transition-all duration-300",
                showMainOnMobile ? "flex w-full" : "hidden md:flex"
            )}>
                {children}
            </main>

            {/* Mobile Floating Tab Bar */}
            {!isRoomSelected && (
                <div className="md:hidden fixed bottom-8 left-6 right-6 h-20 bg-black/80 backdrop-blur-3xl rounded-[32px] border border-white/10 flex items-center justify-around px-2 z-50 shadow-2xl animate-in slide-in-from-bottom-10 duration-500">
                    <Link
                        href="/chat"
                        className={cn(
                            "flex flex-col items-center gap-1.5 p-3 rounded-2xl transition-all",
                            isChatRoot ? "text-primary scale-110" : "text-white/30"
                        )}
                    >
                        <MessageSquare className="w-6 h-6" />
                        <span className="text-[9px] font-black uppercase tracking-widest">Chat</span>
                    </Link>
                    <Link
                        href="/chat/groups"
                        className={cn(
                            "flex flex-col items-center gap-1.5 p-3 rounded-2xl transition-all",
                            pathname.includes('groups') ? "text-secondary scale-110" : "text-white/30"
                        )}
                    >
                        <Users className="w-6 h-6" />
                        <span className="text-[9px] font-black uppercase tracking-widest">Groups</span>
                    </Link>
                    <Link
                        href="/chat/status"
                        className={cn(
                            "flex flex-col items-center gap-1.5 p-3 rounded-2xl transition-all",
                            pathname.includes('status') ? "text-secondary scale-110" : "text-white/30"
                        )}
                    >
                        <Radio className="w-6 h-6" />
                        <span className="text-[9px] font-black uppercase tracking-widest">Whisper</span>
                    </Link>
                    <Link
                        href="/chat/calls"
                        className={cn(
                            "flex flex-col items-center gap-1.5 p-3 rounded-2xl transition-all",
                            pathname.includes('calls') ? "text-primary scale-110" : "text-white/30"
                        )}
                    >
                        <Phone className="w-6 h-6" />
                        <span className="text-[9px] font-black uppercase tracking-widest">Calls</span>
                    </Link>
                </div>
            )}
        </div>
    );
}
